import base from '@/services/servicio-base'


const servicio  = {}

servicio.obtenerUbicacion = (codPlanta) =>{
    return base.get(`/planta/ubicacion/${codPlanta}`)
    .then(r=>{
        return r.data;
    })
    .catch(e=>{
        return e;
    })
}
servicio.obtenerPlanta = (codPlanta)=>{
    codPlanta = codPlanta == null || codPlanta == undefined ? 0 : codPlanta;
    return base.get(`/planta/${codPlanta}`)
    .then(r=>{
        return r.data;
    }).catch(e=>{
        return e;
    })
}

export default servicio